* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #f1f5f8;
  color: #222;
  position: relative;
}

.booklist {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
}

@media screen and (min-width: 768px) {
  .booklist {
    grid-template-columns: repeat(3, 1fr);
  }
}
.book {
  background: #fff;
  border-radius: 1rem;
  padding: 4rem;
  text-align: center;
  position: relative;
}
.book img {
  width: 100%;
  object-fit: cover;
}
.book h2 {
  margin-top: 1rem;
  font-size: 1rem;
  color: #617d98;
  font-size: 0.75rem;
  margin-top: 0.5rem;
}
.num {
  position: absolute;
  top: 0;
  left: 0;
  padding: 1rem;
  font-weight: bold;
  color: white;
  background-color: #e43b10f8;
  border-radius: 1rem 0rem;
}
.form {
  display: flex;
  flex-direction: column;
  padding: 5rem 4rem 0rem;
}
input {
  border-radius: 0.4rem;
  padding: 0.5rem;
  border: 0.1rem solid black;
}
input[type="text"] {
  width: 30%;
}
input[type="submit"] {
  margin-left: 1rem;
  color: white;
  background-color: #617d98;
}
@media (max-width: 800px) {
  input[type="text"] {
    width: unset;
  }
}
h1 {
  font-size: 40px;
  margin-bottom: 1rem;
}
